@import url(https://fonts.cdnfonts.com/css/pf-tempesta-seven);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #FFFFFF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body {
    background-image: url(/static/media/Background2.58574e41.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: relative;
    font-family: 'PF Tempesta Seven', sans-serif;
    font-family: 'PF Tempesta Seven Extended', sans-serif;
    font-family: 'PF Tempesta Seven Condensed', sans-serif;
    font-family: 'PF Tempesta Seven Compressed', sans-serif;
}

.box {
    border-radius: 75px;
    background: #FFFAB0;
    box-shadow: inset 0 0 0em darkgoldenrod, 0 0 1em black;
    padding: auto;
    margin: auto;
    width: 400px;
    height: 400px;
    -webkit-text-stroke: 1.5px black;
    color: #FAEDDA;
}

.info {
    -webkit-text-stroke: 1.5px black;

}

.wallet {
    border-radius: 25px;
    background: #FFFAB0;
    margin: auto;
    text-align: center;
    font-size: 14px;
    width: 200px;
    height: 70px;
}

.Gif {
    display: block;
    
    left: 74px;
}







